.flickity-viewport,
.flickity-slider {
  position: relative;
  height: 100%; }

.Slides {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100% !important;
  height: 100% !important; }

.Slide {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding: 0 !important; }

.Slide-content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  overflow-y: auto;
  overflow-x: hidden; }

.Slide-contentItem {
  width: 100%; }

.Slide-icon {
  margin: 0 !important;
  border: none !important;
  background: none !important;
  box-shadow: none !important;
  font-size: 50px;
  line-height: 53px; }

.Slide-contentParagraph {
  max-width: 400px; }

@media only screen and (min-width: 0) and (max-width: 450px) {
  .Slide-contentParagraph {
    max-width: 85%; } }

